import React from 'react'
import LayoutWithSidebar from '../components/LayoutWithSidebar'
import LaunchLayout from '../components/LaunchLayout'


import Button from '../components/Button';

import { useNavigate,  } from "react-router-dom";

import config from '../config';

import {motion} from 'framer-motion'
import ReactPlayer from 'react-player'

import rotateImg from '../assets/images/icon-rotate-indication.png';
import cameraImg from '../assets/images/icon-camera.png';
import playImg from '../assets/images/icon-circle-play.png';
import logoImg from '../assets/images/logo-white.jpg';
import bgUnlocked from '../assets/images/bg-unlocked.png';
import modalTitle from '../assets/images/modal-title-congratz.png';
import modalFooter from '../assets/images/modal-footer.png';
import textTop from '../assets/images/text-container-top.png';
import textBottom from '../assets/images/text-container-bottom.png';


import { useAtom } from 'jotai'
import { atomWithStorage } from 'jotai/utils'

import { currentMapAtom, currentCharacterAtom, lastQRAtom } from '../atoms/index.js';

import { isLandscape } from '../libraries/utils'

export const Unlocked = (props) => {
	let navigate = useNavigate();
	const [currentMap, setCurrentMap] = useAtom(currentMapAtom)
	const [lastQR, setLastQRMap] = useAtom(lastQRAtom)
	const [currentCharacter, setCurrentCharacter] = useAtom(currentCharacterAtom)
  
	React.useEffect(() => {}, [])

	const onBack = () => {
		navigate('/maps')
	}

	const onNext = () => {
		if(currentMap && currentCharacter){
			let cmap = config.PATHS[currentCharacter].find(p => p.key === currentMap)
			setCurrentMap(cmap.next)

			if(!isLandscape()){
				navigate('/landscape')
			} else {
				navigate('/maps')
			}
		}
	}

	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LayoutWithSidebar>
			<div className="h-screen p-[40px] flex justify-center items-center">
			 	<div className='modal-container max-w-[500px]'>
					<img src={modalTitle} className={"w-[101.5%] max-w-[101.5%]"} />
					<div className='p-10'>
						<div className='relative p-4 w-full mb-5'>
							<img src={textTop} className={"w-[124px] absolute left-0 top-0"} />
							<h4>YOU HAVE UNLOCKED THE NEXT LOCATION.</h4>
							<img src={textBottom} className={"w-[124px] absolute right-0 bottom-0"} />
						</div>
						<div  className='flex gap-8'>
							<Button 
								className="btn-white w-[138px]"
								title={"Back"}
								onClick={() => {onBack()}}
							/>
							<Button
								className="btn-white"
								title={"Head to the next location"}
								onClick={() => {onNext()}}
							/>
						</div>
					</div>
					<img src={modalFooter} className={"w-[101.5%] max-w-[101.5%]"} />
				</div>
		 	</div>
		 </LayoutWithSidebar>
		</motion.div>
	)
}