import React from 'react'
import LayoutWithSidebar from '../components/LayoutWithSidebar'
import LaunchLayout from '../components/LaunchLayout'


import Button from '../components/Button';
import TermsAndConditions from '../components/TermsAndConditions';

import { useNavigate,  } from "react-router-dom";

import config from '../config';
import {motion} from 'framer-motion'

const Questions = (props) => {
	return(<>Questions</>)
}

export const Faq = (props) => {
	let navigate = useNavigate();

	return (	
		<motion.div
			//initial={{y: 800}}
			//animate={{y: 0}}
			//transition={{ duration: 0.5}}
		>	
		 <LaunchLayout shadowBack logo onClose={() => {navigate(-1)}}>
			<div className="h-screen pt-[100px] px-[44px]">
				<div className="max-h-[500px] overflow-auto text-center">
					<p className="text-[16px] uppercase mb-3">FAQ</p>
					<Questions />
				</div>
				 	

		 	</div>
		 </LaunchLayout>
		</motion.div>
	)
}