import React from 'react'
import LayoutWithSidebar from '../components/LayoutWithSidebar'
import LaunchLayout from '../components/LaunchLayout'

import Button from '../components/Button';

import { useNavigate } from "react-router-dom";

import config from '../config';

import {motion} from 'framer-motion'
 
export const Narrative = (props) => {
	let navigate = useNavigate();

	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LaunchLayout shadowBack logo onBack={() => {navigate('/terms')}}>
			 <div className="content pt-[100px] px-[25px] flex flex-col justify-between">
				<h1 className='text-white'>Venom is on the loose and Kraven is on the hunt</h1>
				<div className='text-center'>
					<p className="mb-10">Use this website to explore Brooklyn. discover clues, and solve challgenes to complete the experience for a chance to win exclusive prizes.</p>
					<Button 
						className="btn-white"
						title={"Continue"}
						onClick={() => {navigate('/choose')}}
					/>
				</div>
		 	</div>
		 </LaunchLayout>
		</motion.div>
	)
}