import React from 'react'
import LaunchLayout from '../components/LaunchLayout'
import Button from '../components/Button';
import { useNavigate,  } from "react-router-dom";
import config from '../config';
import {motion} from 'framer-motion'
 

import rotateImg from '../assets/images/icon-rotate-indication.png';
import { isLandscape } from '../libraries/utils'

export const Landscape = (props) => {
	let navigate = useNavigate();

	React.useEffect(() => {
		const gotoMapIfLandscape = () => {
			if(isLandscape()){
				navigate('/maps')
				return
			}
		}
		window.addEventListener("resize", gotoMapIfLandscape);

	    return () => window.removeEventListener('resize', gotoMapIfLandscape)
	}, [])

	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LaunchLayout shadowBack fullBlack logo>
				<div className="content px-[40px] flex flex-col items-center justify-center text-white gap-8 text-center" onClick={()=>{navigate('/maps')}}>
					<img src={rotateImg} className={"w-[107px]"}/>
					<p>Rotate your device to landscape mode for optimal viewing experience.</p>
				</div>
		 </LaunchLayout>
		</motion.div>
	)
}