export const Choose = (props) => {
	let navigate = useNavigate();
	let [permissionError, setPermissionError] = React.useState("no error")
	const forceCamera = () => {
	  if(!navigator.mediaDevices) {
	  	setPermissionError("NO MEDIA")
	  	return
		}
	  navigator.mediaDevices
	  .getUserMedia({video: true, audio: false})
	  .then((stream) => {
	    var video = document.querySelector('video');
	    video.src = window.URL.createObjectURL(stream);
	    setPermissionError("stream")
	    video.onloadedmetadata = function(e) {
	    };
	  })
	  .catch((err) => {	    
	    setPermissionError(err?.message)
	  });

	}

  React.useEffect(() => {
    //console.log("navigator", navigator)
    //forceCamera()
  }, [])

	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LaunchLayout shadowBack fullBlack logo onBack={() => {navigate(-1)}}>
			 	<div className="content pt-[100px] px-[25px] flex flex-col justify-between">
				 	<h1 className='text-white'>Scan a <br></br> QR Code</h1>
				 	<div className="flex justify-center gap-10">
				 		<img src={spiderRedImg} className={"h-32 w-32"}/>
				 		<img src={spiderWhiteImg} className={"h-32 w-32"}/>
				 	</div>
					<div className='text-center'>
						<p className="uppercase font-bold mb-5">Choose your adventure</p>
						<p className="">Scan one of the QR codes on the subway to explore the path of Peter or Miles</p>
						<img src={cameraImg} className={"w-[63px] mx-auto py-5"} onClick={() => {
							navigate('/scanqr')
						}}/>
						{config.TESTING && <small className='text-[8px]'>{permissionError}</small>}
					 </div>
				</div>
		 </LaunchLayout>
		</motion.div>
	)
}