import React  from 'react';
import Loader from './Loader';
import { useNavigate } from "react-router-dom";
import splash from '../assets/images/splash.jpg';
import backImg from '../assets/images/icon-circle-arrow-left.png';
import closeImg from '../assets/images/icon-circle-close.png';

import logoImg from '../assets/images/logo-white.png';

export default function LaunchLayout({ main, header, container, loading, children, shadowBack, onClose, onBack, fullBlack, logo }) {
	const back_opacity = fullBlack ? 1.0 : 0.85
  return (
  	<div 
  		style={{ backgroundImage: `url(${splash})` }} 
  		className={`flex-1 overflow-y-auto px-0 min-h-screen bg-cover bg-center`}>

  			{shadowBack && 
				<div style={{ backgroundColor: `rgba(7, 27, 38, ${back_opacity})` }} className={`flex-1 overflow-y-auto px-0 min-h-screen`}>
					<div className={"absolute top-3 px-3 w-full"}>
	  				{onBack && <img src={backImg} className="w-[52px] float-left" onClick={onBack}/>}
	  				{onClose && <img src={closeImg} className="w-[52px] float-right" onClick={onClose}/>}
					</div>
					{children}

					{logo && <div className='p-3 relative w-full text-center'>
					 	<img src={logoImg} className={"w-[150px] mx-auto"}/>
						<span className="text-white text-[8px] text-opacity-30 absolute left-3 bottom-3">© 2023 Marvel</span>
					</div>}
  			</div>}

  			{!shadowBack && <>
	  			<div className={"absolute top-3 px-3 w-full"}>
	  				{onBack && <img src={backImg} className="w-[52px] float-left" onClick={onBack}/>}
	  				{onClose && <img src={closeImg} className="w-[52px] float-right" onClick={onClose}/>}
	  			</div>
  				{children}

					{logo && <div className='p-3 relative w-full text-center'>
					 	<img src={logoImg} className={"w-[150px] mx-auto"}/>
						<span className="text-white text-[8px] text-opacity-30 absolute left-3 bottom-3">© 2023 Marvel</span>
					</div>}
  			</>}
  	
  	</div>
  )
}

