import React from 'react'
import LayoutWithSidebar from '../components/LayoutWithSidebar'
import LaunchLayout from '../components/LaunchLayout'


import Button from '../components/Button';

import { useNavigate,  } from "react-router-dom";

import config from '../config';

import {motion} from 'framer-motion'
import ReactPlayer from 'react-player'

import m1_adidas from '../assets/maps/m1_adidas.mp4';
import m2_bait from  '../assets/maps/m2_bait.mp4';
import m3_warby from '../assets/maps/m3_warby.mp4';
import m4_loft from  '../assets/maps/m4_loft.mp4';
import p2_adidas from '../assets/maps/p2_adidas.mp4';
import p1_bait from  '../assets/maps/p1_bait.mp4';
import p3_warby from '../assets/maps/p3_warby.mp4';
import p4_loft from  '../assets/maps/p4_loft.mp4';

import rotateImg from '../assets/images/icon-rotate-indication.png';
import cameraImg from '../assets/images/icon-camera.png';
import playImg from '../assets/images/icon-circle-play.png';
import logoImg from '../assets/images/logo-white.png';

import { useAtom } from 'jotai'
import { atomWithStorage } from 'jotai/utils'

import { currentMapAtom, currentCharacterAtom, lastQRAtom } from '../atoms/index.js';

import bgSideQuest from '../assets/images/bg-side-quest.png';
import spider1Img from '../assets/images/spider-1.png';
import spider2Img from '../assets/images/spider-2.png';
import spider3Img from '../assets/images/spider-3.png';


const mapsVideos = {
	"m1_adidas": m1_adidas,
	"m2_bait": m2_bait,
	"m3_warby": m3_warby,
	"m4_loft": m4_loft,
	"p2_adidas": p2_adidas,
	"p1_bait": p1_bait,
	"p3_warby": p3_warby,
	"p4_loft": p4_loft,
}

const SideQuestModal = () => {
	return (
		<>
			<img src={bgSideQuest} className="w-full" />
    	</>
	)
}

export const Maps = (props) => {
	let navigate = useNavigate();
	const videoRef = React.useRef();
	const [currentMap, setCurrentMap] = useAtom(currentMapAtom)
	const [currentCharacter, setCurrentCharacter] = useAtom(currentCharacterAtom)

	const [videoLoops, setVideoLoops] = React.useState(0)
	const [videoPlaying, setVideoPlaying] = React.useState(true)
	const [showButtons, setShowButtons] = React.useState(false)
	const [currentVideo, setCurrentVideo] = React.useState(null)
	const [last, setLast] = React.useState(false)
	const [final, setFinal] = React.useState(false)

	React.useEffect(() => {
		if(!currentMap && !currentCharacter){
			navigate('/')
			return
		}
		if(currentMap && currentCharacter){
			let cmap = config.PATHS[currentCharacter].find(p => p.key === currentMap)
			if(cmap.side){
				document.getElementById('side_quest_modal').showModal()
				console.log('TIMER CREATED')
				setTimeout(() => {
  					document.getElementById('side_quest_modal').close()
  					setVideoPlaying(true)
  					setLast(true)
				}, config.SIDEQUEST_TIMTER_SECONDS*1000);

				setVideoPlaying(false)
			}

			if(cmap.final){
				setFinal(true)
			}
			setCurrentVideo(mapsVideos[cmap.video])
		}
	}, [])

	const replay = () => {
		setVideoLoops(0)
		setShowButtons(false)
		setVideoPlaying(true)
	}


	const onEnded = () => {
		if(videoLoops === config.MAP_VIDEO_LOOPS){
			setVideoPlaying(false)
			if(final){
				navigate('/final')
				return				
			}
			if(last){		    
				navigate('/congratulations')
				return
			}

			setShowButtons(true)

			
		} else {
			setVideoLoops(videoLoops+1)
			videoRef.current.seekTo(0)
		}
	}

	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LayoutWithSidebar>
			<div className='h-screen'>
				<dialog id="side_quest_modal" className="modal">
				  <div className="modal-box">
				    <SideQuestModal />
				  </div>
				</dialog>
				{!currentVideo && config.TESTING && <p>Error: no current video</p>}
			 	{currentVideo && <ReactPlayer url={currentVideo} onEnded={onEnded} playing={videoPlaying} loop={false} ref={videoRef} controls={false} width="100%" height="100%"/>}
		 		{/* <img src={logoImg} className={"h-16 w-32 absolute bottom-0 left-0"}/> */}
		 		{showButtons && 
					<div className="flex gap-6 absolute bottom-[15px] right-[95px] z-20">
						<img src={cameraImg} className={"w-[60px] h-[60px]"} onClick={() => {navigate('/scanqr')}}/>
						<img src={playImg} className={"w-[60px] h-[60px]"} onClick={() => {replay()}}/>
					</div>
		 		}
		 	</div>
		 </LayoutWithSidebar>
		</motion.div>
	)
}