import React from 'react'
import LayoutWithSidebar from '../components/LayoutWithSidebar'
import LaunchLayout from '../components/LaunchLayout'


import Button from '../components/Button';

import { useNavigate,  } from "react-router-dom";

import config from '../config';

import {motion} from 'framer-motion'

import { useAtom } from 'jotai'
import { atomWithStorage } from 'jotai/utils'

import { currentMapAtom, currentCharacterAtom, lastQRAtom } from '../atoms/index.js';

export const Home = (props) => {
	let navigate = useNavigate();

	const [currentCharacter, setCurrentCharacter] = useAtom(currentCharacterAtom)
	const [currentMap, setCurrentMap] = useAtom(currentMapAtom)
	
	React.useEffect(() => {
		if(currentCharacter && currentMap && !config.TESTING){
			navigate('/maps')
		}
	})


	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LaunchLayout>
			 <div className="flex items-end justify-center content">
			 	<div className="flex flex-col text-center uppercase text-white">
					<h3 className="mb-4">Welcome to</h3>
				 	<h2 className='mb-10'> great hunt</h2>
				 	<Button 
						className="btn-white"
				 		title={"Start Adventure"}
				 		onClick={() => {navigate('/terms')}}
				 	/>
				 	<small className="text-white text-[8px] text-opacity-30 absolute left-3 bottom-3">© 2023 Marvel</small>
				 </div>

		 	</div>
		 </LaunchLayout>
		</motion.div>
	)
}